.section__title {
    font-size: 32px;
    line-height: 1.19;
    text-align: center;
}

.section-blog_title {
    margin-bottom: 23px;
}

.section__exhortation {
    font-size: 18px;
    line-height: 1.36;
    text-align: center;
}

.blog-card {
    display: flex;
    max-width: 100%;
    min-height: 370px;
    background-color: #231F20;
    color: #FFFFFF;

    &_info {
        display: flex;
        flex-direction: column;

        width: 100%;
        padding: 63px 78px 49px 78px;
    }

    &_author {
        margin-bottom: 27px;

        font-size: 14px;
        line-height: 1.41;

    }

    &_title {
        font-size: 48px;
        line-height: 1.21;
    }

    &_link {
        margin-top: auto;
        font-size: 18px;
        line-height: 1.41;
        color: #FFFFFF;
    }
}

.section-blog_blog-card {
    width: 100%;
    margin-top: 80px;
    margin-bottom: 40px;
}

.blog-block {

    display: flex;
    flex-direction: row;

    .blog-card-sm_title {
        margin-bottom: 16px;
    }
}



.blog-card-sm {
    display: flex;
    flex-direction: column;
    min-height: 290px;


    padding: 48px;
    line-height: 1.41;
    color: black;
    

    &:hover, &:focus {
       box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05); 
    }

    &_title {
        font-size: 24px;

    }

    &_text {
        font-size: 18px;
    }

    &_link {
        margin-top: auto;
        font-size: 14px;
        color: black;
    }
}